import {Flash} from '@primer/react'

interface ReadmeCalloutBannerProps {
  name: string
  owner: string
}

export function ProfileReadmeBanner({name, owner}: ReadmeCalloutBannerProps) {
  return (
    <Flash variant="success" sx={{mb: 3}}>
      <strong>{`${owner}/${name}`}</strong> is a special repository: its <code>README.md</code> will appear on your
      profile!
    </Flash>
  )
}

export function OrgProfileReadmeBanner({name, owner}: ReadmeCalloutBannerProps) {
  return (
    <Flash variant="success" sx={{mb: 3}}>
      <strong>{`${owner}/${name}`}</strong> is a special repository: this <code>README.md</code> will appear on your
      public organization profile, visible to anyone.
    </Flash>
  )
}

export function OrgMemberProfileReadmeBanner({name, owner}: ReadmeCalloutBannerProps) {
  return (
    <Flash variant="success" sx={{mb: 3}}>
      <strong>{`${owner}/${name}`}</strong> is a special repository: this <code>README.md</code> will appear on your
      organization member profile, visible only to organization members.
    </Flash>
  )
}

try{ ProfileReadmeBanner.displayName ||= 'ProfileReadmeBanner' } catch {}
try{ OrgProfileReadmeBanner.displayName ||= 'OrgProfileReadmeBanner' } catch {}
try{ OrgMemberProfileReadmeBanner.displayName ||= 'OrgMemberProfileReadmeBanner' } catch {}