import type {DiffDislayInfo, DiffLines, DiffResponse, EditInfo, FundingData} from '@github-ui/code-view-types'
import FileRendererBlob from '@github-ui/file-renderer-blob'
import type {RefInfo} from '@github-ui/repos-types'
import {SafeHTMLBox, type SafeHTMLString} from '@github-ui/safe-html'
import {useCSRFToken} from '@github-ui/use-csrf-token'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {Box, Spinner, Text} from '@primer/react'
import React from 'react'

import {CodeLine} from '../blob/BlobContent/Code/CodeLine'
import type {CodeLineData} from '../blob/BlobContent/Code/hooks/use-code-lines'
import {LineNumber} from '../blob/BlobContent/Code/LineNumber'
import {DiffPreview} from './DiffPreview'
import {FundingDialog} from './FundingDialog'

let previousController: AbortController | null = null

export enum PreviewMessages {
  DELETED = 'preview_deleted',
  RENAMED = 'preview_renamed',
  UNCHANGED = 'preview_unchanged',
  ERROR = 'preview_error',
  BLANK = 'preview_blank',
}

export function EditorPreview({
  editInfo,
  fileContent,
  fileName,
  refInfo,
  showMarkdownDiff,
  isRichtextRenderable,
}: {
  editInfo: EditInfo
  fileContent: string
  fileName: string
  refInfo: RefInfo
  showMarkdownDiff: boolean
  isRichtextRenderable: boolean
}) {
  const [previewHTML, setPreviewHTML] = React.useState<SafeHTMLString | undefined>(undefined)

  const [diffData, setDiffData] = React.useState<DiffLines[] | undefined>(undefined)

  const [blobData, setBlobData] = React.useState<CodeLineData[] | undefined>(undefined)

  const [message, setMessage] = React.useState<{message: string; messageHTML: string} | undefined>(undefined)

  const [displayData, setDisplayData] = React.useState<DiffDislayInfo | undefined>(undefined)

  const previewToken = useCSRFToken(editInfo.previewEditPath, 'post')
  const [fundingData, setFundingData] = React.useState<FundingData | undefined>(undefined)

  React.useEffect(() => {
    const fetchPreview = async () => {
      previousController?.abort()
      const {signal} = (previousController = new AbortController())

      const formData = new FormData()
      formData.append('code', fileContent)
      formData.append('commit', refInfo.currentOid)
      formData.append('blobname', fileName)
      // This doesn't actually do anything in preview, based on the "quick pull" option in web commit dialog
      formData.append('willcreatebranch', 'false')
      // Only false for Gists
      formData.append('checkConflict', 'true')
      // eslint-disable-next-line github/authenticity-token
      formData.append('authenticity_token', previewToken || '')

      setDiffData(undefined)
      setMessage(undefined)
      setPreviewHTML(undefined)
      setDisplayData(undefined)
      setFundingData(undefined)

      if (isRichtextRenderable) {
        const response = await verifiedFetch(
          !showMarkdownDiff ? `${editInfo.previewEditPath}?avoiddiff=true` : editInfo.previewEditPath,
          {
            method: 'POST',
            body: formData,
            signal,
            headers: {
              Accept: 'application/json',
            },
          },
        )

        const json: DiffResponse = await response.json()

        const html = json.data.html || ''
        // For avoiddiff=true, we don't get the message in the message but in the html
        const markdownMessage = getPreviewMessage(html)

        if (json.data.message) {
          setMessage({message: json.data.message, messageHTML: json.data.messageHtml ?? ''})
        } else if (markdownMessage !== html) {
          setMessage({message: markdownMessage, messageHTML: ''})
        } else {
          setPreviewHTML(html || ('Error getting preview' as SafeHTMLString))
        }
      } else {
        const response = await fetch(editInfo.previewEditPath, {
          method: 'POST',
          body: formData,
          signal,
          headers: {
            Accept: 'application/json',
          },
        })

        const json: DiffResponse = await response.json()

        if (json.data.message) {
          setMessage({message: json.data.message, messageHTML: json.data.messageHtml ?? ''})
        } else if (json.data.diffLines && json.data.diffLines.length > 0) {
          const lines = json.data.diffLines
          for (const line of lines) {
            if (line.rightLine.type === 'ADDITION') {
              line.rightLine.html = `+${line.rightLine.html}`
            } else {
              line.rightLine.html = ` ${line.rightLine.html}`
            }
            if (line.leftLine.type === 'DELETION') {
              line.leftLine.html = `-${line.leftLine.html}`
            } else {
              line.leftLine.html = ` ${line.leftLine.html}`
            }
          }
          setDiffData(lines)
        } else if (json.data.rawLines && json.data.rawLines.length > 0) {
          setBlobData(
            json.data.rawLines.map((line, i) => ({
              lineNumber: i + 1,
              rawText: line,
              stylingDirectivesLine: json.data.stylingDirectives?.[i],
            })),
          )
        } else if (json.data.displayData) {
          setDisplayData(json.data.displayData)
        } else if (json.data.fundingLinks) {
          setFundingData(json.data.fundingLinks)
        } else {
          setMessage({message: 'There has been an error generating the preview.', messageHTML: ''})
        }
      }
    }

    fetchPreview()

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [showMarkdownDiff])

  return (
    <>
      {previewHTML ? (
        <div className={`readme html-blob blob ${showMarkdownDiff ? 'prose-diff' : 'p-5'}`}>
          <SafeHTMLBox className="markdown-body container-lg" html={previewHTML} />
        </div>
      ) : message ? (
        <Box sx={{p: 5, position: 'relative'}}>
          <Text sx={{fontSize: 4, lineHeight: '1.5', wordWrap: 'break-word'}}>
            {getPreviewMessage(message.message)}
            {message.messageHTML && ' But you can preview the whole file.'}
          </Text>
          <SafeHTMLBox
            className={isRichtextRenderable ? 'markdown-body' : undefined}
            unverifiedHTML={message.messageHTML || ''}
            sx={{mt: 4}}
          />
        </Box>
      ) : diffData && diffData.length ? (
        <DiffPreview diffData={diffData} fileName={fileName} />
      ) : blobData && blobData.length ? (
        <Box className="react-code-file-contents" sx={{p: 3, overflowX: 'auto'}}>
          <div className="react-line-numbers">
            {blobData.map((lineData, index) => (
              <LineNumber key={index} codeLineData={lineData} preventClick />
            ))}
          </div>
          <div className="react-code-lines">
            {blobData.map((lineData, index) => (
              <CodeLine
                stylingDirectivesLine={lineData.stylingDirectivesLine}
                key={index}
                codeLineData={lineData}
                forceVisible
                copilotAccessAllowed={false}
                shouldUseInert={false}
              /> // deliberately don't show copilot button
            ))}
          </div>
        </Box>
      ) : displayData ? (
        <FileRendererBlob
          url={displayData.displayUrl}
          identityUuid={displayData.identityUuid}
          type={displayData.renderFileType}
          size={displayData.size}
        />
      ) : fundingData ? (
        <FundingDialog fileName={fileName} fundingData={fundingData} />
      ) : (
        <Box sx={{display: 'flex', flexDirection: 'column', alignItems: 'center', py: 3}}>
          <Spinner />
          Loading preview…
        </Box>
      )}
    </>
  )
}

function getPreviewMessage(message: string) {
  switch (message) {
    case PreviewMessages.DELETED:
      return 'The document has been deleted.'
    case PreviewMessages.RENAMED:
      return 'The document was renamed without changes.'
    case PreviewMessages.UNCHANGED:
      return 'There are no changes to show.'
    case PreviewMessages.BLANK:
      return 'There is no content to preview.'
    case PreviewMessages.ERROR:
      return 'There has been an error generating the preview.'
    default:
      return message
  }
}

try{ EditorPreview.displayName ||= 'EditorPreview' } catch {}