import type {Diagnostic} from '@codemirror/lint'
import type {EditorView} from '@codemirror/view'
import {posToOffset, replaceRange} from '@github-ui/code-mirror/utils'
import {html, render} from 'lit-html'
import type {LintMessage} from './codemirror-linter-util'

export function buildLintMarks(view: EditorView, annotations: LintMessage[]): Diagnostic[] {
  const diagnostics: Diagnostic[] = annotations.map(annotation => {
    const fromOffset = posToOffset(view, annotation.from)
    const toOffset = posToOffset(view, annotation.to)

    // helper if the annotation has fixes available
    const replaceValue = (value: string) => {
      return replaceRange(view, fromOffset, toOffset, value)
    }

    return {
      from: fromOffset,
      to: toOffset,
      message: '', // handled by renderMessage
      severity: annotation.severity as 'info' | 'warning' | 'error',
      renderMessage: () => {
        const tooltip = createTooltip(annotation)
        const select = tooltip.querySelector<HTMLSelectElement>('select')!
        const button = tooltip.querySelector<HTMLElement>('button')!

        button.addEventListener('click', () => replaceValue(button.textContent!))
        select.addEventListener('change', () => replaceValue(select.value))

        return tooltip
      },
    }
  })

  return diagnostics
}

export function createTooltip(lintMessage: LintMessage): HTMLElement {
  const {message, fixes} = lintMessage
  const tooltipTemplate = html`<div class="Box Box--condensed text-small color-shadow-large js-yaml-lint">
    <div class="js-yaml-lint-header Box-row markdown-body f6">
      <p class="mb-0 text-bold js-yaml-lint-title" />
      <p class="color-fg-muted js-yaml-lint-subtitle">
        Did you mean <button class="btn-link btn-sm">todo-here</button>?
      </p>
    </div>
    <div class="Box-footer color-bg-subtle markdown-body f6 js-yaml-lint-message"></div>
    <div class="Box-footer color-bg-subtle js-yaml-lint-fixes">
      <select class="form-select select-sm" aria-label="Suggested fixes">
        <option>Other suggestions…</option>
      </select>
    </div>
  </div>`

  const tooltip = document.createElement('div')
  render(tooltipTemplate, tooltip)

  const tooltipHeader = tooltip.querySelector<HTMLElement>('.js-yaml-lint-header')!
  const tooltipTitle = tooltip.querySelector<HTMLElement>('.js-yaml-lint-title')!
  const tooltipSubtitle = tooltip.querySelector<HTMLElement>('.js-yaml-lint-subtitle')!
  const tooltipMessage = tooltip.querySelector<HTMLElement>('.js-yaml-lint-message')!
  const tooltipFixes = tooltip.querySelector<HTMLElement>('.js-yaml-lint-fixes')!
  const fixButton = tooltipSubtitle.querySelector<HTMLElement>('button')!

  tooltipHeader.hidden = !fixes
  tooltipMessage.hidden = !!fixes
  tooltipSubtitle.hidden = !fixes
  tooltipFixes.hidden = !fixes || fixes.length < 2

  if (fixes) {
    render(message, tooltipTitle)
    const firstFix = fixes[0]!
    fixButton.textContent = firstFix
    const select = tooltipFixes.querySelector<HTMLElement>('select')!
    for (const fix of fixes.slice(1)) {
      const option = document.createElement('option')
      option.textContent = fix
      select.appendChild(option)
    }
  } else {
    render(message, tooltipMessage)
  }

  return tooltip
}
