import {linkButtonSx} from '@github-ui/code-view-shared/utilities/styles'
import {Link} from '@github-ui/react-core/link'
import {CodeOfConductIcon} from '@primer/octicons-react'
import {Button, type ButtonProps} from '@primer/react'

export default function CodeOfConductSelector({url, sx}: {url: string; sx?: ButtonProps['sx']}) {
  return (
    <Button
      variant="invisible"
      as={Link}
      sx={{
        color: 'fg.default',
        '> span[data-component=buttonContent]': {justifyContent: 'start'},
        ...linkButtonSx,
        ...sx,
      }}
      to={url}
      leadingVisual={CodeOfConductIcon}
    >
      Choose a code of conduct template
    </Button>
  )
}

try{ CodeOfConductSelector.displayName ||= 'CodeOfConductSelector' } catch {}