import {SafeHTMLBox, type SafeHTMLString} from '@github-ui/safe-html'

export function ServerSideDocumentation({documentation}: {documentation?: string}) {
  return <SafeHTMLBox html={documentation as SafeHTMLString} className="f5 markdown-body" sx={{p: 3}} />
}

try{ ServerSideDocumentation.displayName ||= 'ServerSideDocumentation' } catch {}