import type {CodeMirrorSpacingOptions} from '@github-ui/code-view-types'
import {Box, FormControl, Select} from '@primer/react'
import type {BetterSystemStyleObject} from '@primer/react/lib-esm/sx'
import type React from 'react'
import {useState} from 'react'

export const defaultCodeMirrorSpacingOptions: CodeMirrorSpacingOptions = {
  indentSize: '2',
  indentMode: 'space',
  lineWrapping: 'off',
}

interface CodeMirrorSpacingControlsProps {
  options: CodeMirrorSpacingOptions
  onChange: (options: CodeMirrorSpacingOptions) => void
  sx?: Record<string, unknown>
  formControlSx?: CodeMirrorIndentModeControlProps['sx']
}

export function CodeMirrorSpacingControls({options, onChange, sx, formControlSx}: CodeMirrorSpacingControlsProps) {
  const [indentMode, setIndentMode] = useState(options.indentMode)
  const [indentSize, setIndentSize] = useState(options.indentSize)
  const [lineWrapping, setLineWrapping] = useState(options.lineWrapping)

  const onIndentModeChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const updatedIndentMode = event.target.value as CodeMirrorSpacingOptions['indentMode']
    setIndentMode(updatedIndentMode)
    onChange({indentMode: updatedIndentMode, indentSize, lineWrapping})
  }

  const onIndentSizeChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const updatedIndentSize = event.target.value as CodeMirrorSpacingOptions['indentSize']
    setIndentSize(updatedIndentSize)
    onChange({indentMode, indentSize: updatedIndentSize, lineWrapping})
  }

  const onLineWrappingChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const updatedLineWrapping = event.target.value as CodeMirrorSpacingOptions['lineWrapping']
    setLineWrapping(updatedLineWrapping)
    onChange({indentMode, indentSize, lineWrapping: updatedLineWrapping})
  }

  return (
    <Box sx={{justifySelf: 'flex-end', display: 'flex', gap: 2, ...sx}}>
      <CodeMirrorSpacingControl
        onChange={onIndentModeChange}
        value={indentMode}
        label="Indent mode"
        options={[
          {value: 'spaces', label: 'Spaces'},
          {value: 'tab', label: 'Tabs'},
        ]}
        sx={formControlSx}
      />
      <CodeMirrorSpacingControl
        onChange={onIndentSizeChange}
        value={indentSize}
        label="Indent size"
        options={[
          {value: '2', label: '2'},
          {value: '4', label: '4'},
          {value: '8', label: '8'},
        ]}
        sx={formControlSx}
      />
      <CodeMirrorSpacingControl
        onChange={onLineWrappingChange}
        value={lineWrapping}
        label="Line wrap mode"
        options={[
          {value: 'off', label: 'No wrap'},
          {value: 'on', label: 'Soft wrap'},
        ]}
        sx={formControlSx}
      />
    </Box>
  )
}

interface CodeMirrorIndentModeControlProps {
  onChange: React.ChangeEventHandler<HTMLSelectElement>
  value: string | undefined
  label: string
  options: Array<{value: string; label: string}>
  sx?: BetterSystemStyleObject
}

function CodeMirrorSpacingControl({onChange, value, label, options, sx}: CodeMirrorIndentModeControlProps) {
  return (
    <FormControl sx={sx}>
      <FormControl.Label visuallyHidden>{label}</FormControl.Label>
      <Select size="small" onChange={onChange} value={value} aria-label={label}>
        <Select.OptGroup label={label}>
          {options.map(option => (
            <Select.Option key={option.value} value={option.value}>
              {option.label}
            </Select.Option>
          ))}
        </Select.OptGroup>
      </Select>
    </FormControl>
  )
}

try{ CodeMirrorSpacingControls.displayName ||= 'CodeMirrorSpacingControls' } catch {}
try{ CodeMirrorSpacingControl.displayName ||= 'CodeMirrorSpacingControl' } catch {}