import {verifiedFetch} from '@github-ui/verified-fetch'
import {CheckIcon} from '@primer/octicons-react'
import {Box, Button, Dialog, Flash, Octicon} from '@primer/react'
import React, {useEffect, useState} from 'react'
import {useQuery} from '@tanstack/react-query'

// eslint-disable-next-line no-restricted-imports
import {CodeEditorExtensions as SecretScanningPushProtection} from '../../../github/secret-scanning/push-protection'
import {AllowSecretBody} from './AllowSecretBody'
import {type BypassMetadata, BypassReason} from '@github-ui/secret-scanning'
import {promotePushProtectionBypassPath, requestPushProtectionBypassPath} from '../../paths'

interface IBypassSecretData {
  value: string
  reason: string
  parent_action: string
  bypass_placeholder_ksuid: string
}

export function SecretDetectedDialogBlob({
  bypassMetadata,
  content,
  helpUrl,
}: {
  bypassMetadata: BypassMetadata
  content: string
  helpUrl: string
}) {
  const [isOpen, setIsOpen] = useState(true)
  const closeOverlay = () => setIsOpen(false)

  const [selectedRadioValue, setSelectedRadioValue] = React.useState('')
  const handleRadioGroupChange = (selected: string | null) => {
    setSelectedRadioValue(selected ?? '')
  }

  const promoteBypassPath = promotePushProtectionBypassPath({
    ownerDisplayLogin: bypassMetadata.owner_display_login,
    repoName: bypassMetadata.repo_name,
    ksuid: bypassMetadata.placeholder_ksuid,
  })

  const requestBypassPath = requestPushProtectionBypassPath({
    ownerDisplayLogin: bypassMetadata.owner_display_login,
    repoName: bypassMetadata.repo_name,
    ksuid: bypassMetadata.placeholder_ksuid,
    ruleSuiteId: bypassMetadata.rule_suite_id || 0,
  })

  const {
    isSuccess: unblockSecretSuccess,
    isError: unblockSecretFailed,
    refetch: bypassSecret,
  } = useQuery({
    // eslint-disable-next-line @tanstack/query/exhaustive-deps
    queryKey: [bypassMetadata, selectedRadioValue],
    queryFn: async () => {
      const data: IBypassSecretData = {
        value: content,
        reason: bypassMetadata.limited_user_bypass_experience_only ? BypassReason.WillFixLater : selectedRadioValue,
        bypass_placeholder_ksuid: bypassMetadata.placeholder_ksuid,
        parent_action: 'edit',
      }

      const formData = new FormData()
      for (const [key, dataValue] of Object.entries(data)) {
        if (dataValue !== undefined) {
          formData.set(key, dataValue)
        }
      }

      const result = await verifiedFetch(promoteBypassPath, {
        method: 'post',
        body: formData,
        headers: {
          Accept: 'application/json',
        },
      })

      closeOverlay()

      if (!result.ok) throw new Error('Failed to promote bypass: request failed')

      return result
    },
    enabled: false,
    retry: false,
  })

  useEffect(() => {
    setIsOpen(true)
    setSelectedRadioValue('')
  }, [bypassMetadata])

  if (bypassMetadata.first_secret_location) {
    const codeMirrorEditorElement = document.querySelector<HTMLElement>('.js-codemirror-editor')
    SecretScanningPushProtection.emitAnnotateDiscoveredSecretsWithLocation(
      content,
      bypassMetadata.first_secret_location?.start_line,
      bypassMetadata.first_secret_location?.end_line,
      bypassMetadata.first_secret_location?.start_line_byte_position,
      bypassMetadata.first_secret_location?.end_line_byte_position,
      codeMirrorEditorElement!,
    )
  }

  return (
    <>
      {!unblockSecretSuccess && (
        <Dialog
          isOpen={isOpen}
          onDismiss={() => setIsOpen(false)}
          sx={{width: '546px'}}
          aria-labelledby="secret-detected-header"
        >
          <Box sx={{overflowY: 'auto', mt: 4}}>
            <AllowSecretBody
              onRadioGroupChange={handleRadioGroupChange}
              bypassMetadata={bypassMetadata}
              helpUrl={helpUrl}
              onClickSecretLocationLink={closeOverlay}
            />
            <Box sx={{display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', m: 3}}>
              {bypassMetadata.use_delegated_bypass_flow ? (
                <Button variant="default" as="a" href={requestBypassPath} target="_blank">
                  Start request
                </Button>
              ) : (
                <>
                  <Button sx={{mr: 2}} onClick={closeOverlay}>
                    Cancel
                  </Button>
                  <Button
                    variant="primary"
                    disabled={
                      bypassMetadata.limited_user_bypass_experience_only
                        ? false
                        : !selectedRadioValue || selectedRadioValue.length === 0
                    }
                    onClick={() => {
                      bypassSecret()
                    }}
                  >
                    Allow Secret
                  </Button>
                </>
              )}
            </Box>
          </Box>
        </Dialog>
      )}
      {unblockSecretSuccess && (
        <Flash variant="success" sx={{mb: 3}}>
          <Octicon icon={CheckIcon} />
          Secret allowed. You can now commit these changes.
        </Flash>
      )}
      {unblockSecretFailed && (
        <Flash variant="danger" sx={{mb: 3}}>
          An error occurred when allowing your secret. Please try again.
        </Flash>
      )}
    </>
  )
}

try{ SecretDetectedDialogBlob.displayName ||= 'SecretDetectedDialogBlob' } catch {}