import {Button, Flash, Link} from '@primer/react'

export function CitationFileBanner({
  citationHelpUrl,
  repositoryCitationTemplateUrl,
  setContent,
}: {
  citationHelpUrl: string
  repositoryCitationTemplateUrl: string
  setContent: (content: string) => void
}) {
  const getTemplate = async () => {
    const result = await fetch(repositoryCitationTemplateUrl, {headers: {'X-Requested-With': 'XMLHttpRequest'}})

    setContent(await result.text())
  }

  return (
    <Flash sx={{alignItems: 'center', display: 'flex', justifyContent: 'space-between', mb: 3}}>
      <div>
        Adding a <strong>CITATION.cff</strong> file helps users to easily cite your software from the repository
        overview. &nbsp;
        <Link inline href={citationHelpUrl}>
          Learn more about CITATION files.
        </Link>
      </div>
      <Button onClick={getTemplate} sx={{mr: 2}}>
        Insert example
      </Button>
    </Flash>
  )
}

try{ CitationFileBanner.displayName ||= 'CitationFileBanner' } catch {}