import type {EditorView} from '@codemirror/view'

import {getRootRule as devContainerRootRule} from './devcontainer/devcontainer-rules'
import {lint} from './json-codemirror-linter-util'
import {buildLintMarks} from '../codemirror-next-linter-util'

export async function jsonLintSource(view: EditorView) {
  // devContainerRootRule isnt actually used, we just need to pass something to lint
  const annotations = await lint(view.state.doc.toString(), devContainerRootRule)

  return buildLintMarks(view, annotations)
}
