import {useReposAppPayload} from '@github-ui/code-view-shared/contexts/FilesPageInfoContext'
import type {FundingData} from '@github-ui/code-view-types'
import {GitHubAvatar} from '@github-ui/github-avatar'
import {ownerAvatarPath, ownerPath} from '@github-ui/paths'
import {HeartIcon, LinkIcon} from '@primer/octicons-react'
import {Box, Button, Flash, Link, Octicon, Text} from '@primer/react'

const NON_SPONSORABLE_ERROR = 'Some users provided are not enrolled in GitHub Sponsors.'

export interface FundingDialogProps {
  fileName: string
  fundingData: FundingData
}

export function FundingDialog({fileName, fundingData}: FundingDialogProps) {
  const {helpUrl} = useReposAppPayload()

  const useCondensedLayout = fundingData.sponsorableUsers.length > 1

  return (
    <Box
      sx={{
        width: '448px',
        marginRight: 'auto',
        marginLeft: 'auto',

        borderLeft: '1px solid',
        borderRight: '1px solid',
        borderColor: 'border.default',
      }}
    >
      <Box sx={{px: 3, pt: 3}}>
        {fundingData.errors.length ? (
          <Flash variant="danger" sx={{mb: 3, py: 4}}>
            <p>Some errors were encountered when parsing the {fileName} file:</p>
            <Box as="ul" sx={{listStyleType: 'circle', ml: 4}}>
              {fundingData.errors.map((error, index) => (
                <li key={index}>
                  <span>{error}</span>
                  {error === NON_SPONSORABLE_ERROR && (
                    <>
                      {' '}
                      <Link href="/sponsors/accounts">Apply to Sponsors.</Link>
                    </>
                  )}
                </li>
              ))}
            </Box>
            <Box as="p" sx={{mt: 2}}>
              <Link inline href={`${helpUrl}/articles/displaying-a-sponsor-button-in-your-repository`}>
                Learn more about formatting {fileName}
              </Link>
              .
            </Box>
          </Flash>
        ) : (
          !fundingData.hasValidPlatform && (
            <Flash variant="danger" sx={{mb: 3}}>
              <p>The {fileName} file does not currently contain valid funding links.</p>
              <Box as="p" sx={{mt: 2}}>
                <Link inline href={`${helpUrl}/articles/displaying-a-sponsor-button-in-your-repository`}>
                  Learn more about formatting {fileName}
                </Link>
                .
              </Box>
            </Flash>
          )
        )}
        <Box sx={{display: 'flex', flexDirection: 'column'}}>
          {(fundingData.sponsorableOrg || fundingData.sponsorableUsers.length !== 0) && (
            <Box sx={{display: 'flex', flexDirection: 'column'}}>
              <Box sx={{display: 'flex', flexDirection: 'row', justifyContent: 'space-between', mb: 3}}>
                <h5>GitHub Sponsors</h5>
                <Link href="/sponsors">Learn more</Link>
              </Box>
              {fundingData.sponsorableOrg && <GiHubFundingComponent name={fundingData.sponsorableOrg} isOrg />}
              <Box sx={useCondensedLayout ? {display: 'flex', flexWrap: 'wrap', mx: -2} : undefined}>
                {fundingData.sponsorableUsers.map((user, index) => (
                  <GiHubFundingComponent key={index} name={user} condensed={useCondensedLayout} />
                ))}
              </Box>
            </Box>
          )}
          {fundingData.externalAccounts.length !== 0 && (
            <>
              <Text as="h5" sx={{mb: 3}}>
                External links
              </Text>
              {fundingData.externalAccounts.map((link, index) => (
                <Box key={index} sx={{display: 'flex', mb: 3}}>
                  <Box
                    sx={{
                      alignItems: 'center',
                      justifyContent: 'center',
                      boxSizing: 'border-box',
                      border: '1px solid',
                      borderColor: 'border.default',
                      borderRadius: 3,
                      display: 'flex',
                      mr: 2,
                      width: '24px',
                      height: '24px',
                    }}
                  >
                    {link.platform !== 'Custom' ? (
                      <Box
                        as="img"
                        src={`/images/modules/site/icons/funding_platforms/${link.platform.toLocaleLowerCase()}.svg`}
                        alt={link.platform}
                        sx={{width: '16px', height: '16px', display: 'block'}}
                      />
                    ) : (
                      <Octicon size={16} icon={LinkIcon} sx={{color: 'fg.muted'}} />
                    )}
                  </Box>
                  <Link href={link.link}>{link.link}</Link>
                </Box>
              ))}
            </>
          )}
        </Box>
      </Box>
      <Box sx={{p: 3, fontSize: 0, borderTop: '1px solid', borderColor: 'border.default', boxSizing: 'border-box'}}>
        <>
          <Link inline href={`${helpUrl}/articles/displaying-a-sponsor-button-in-your-repository`}>
            Learn more about funding links in repositories
          </Link>
          .
        </>
        {fundingData.reportAbuseLink && (
          <Link muted href={fundingData.reportAbuseLink}>
            Report abuse
          </Link>
        )}
      </Box>
    </Box>
  )
}

export interface GiHubFundingComponentProps {
  name: string
  condensed?: boolean
  isOrg?: boolean
}

export function GiHubFundingComponent({name, condensed, isOrg}: GiHubFundingComponentProps) {
  let containerSx = {}

  if (condensed) {
    containerSx = {
      // Slighly less than 50% to allow for both cards with a border
      width: '49.99%',
      px: 2,
      float: 'left',
    }
  }

  return (
    <Box sx={{mb: 3, ...containerSx}}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: condensed ? 'column' : 'row',
          justifyContent: 'space-between',
          alignItems: condensed ? undefined : 'center',
          border: '1px solid',
          borderColor: 'border.default',
          borderRadius: '6px',
          padding: 3,
          height: '100%',
        }}
      >
        <Box sx={{display: 'flex', flexDirection: 'row'}}>
          <GitHubAvatar square={isOrg} size={40} src={ownerAvatarPath({owner: name})} sx={{mr: 2, flexShrink: 0}} />

          <Box sx={{display: 'flex', flexDirection: 'column', minWidth: 0}}>
            <Box as="p" sx={{fontWeight: 'bold', m: 0}}>
              <Link href={ownerPath({owner: name})} sx={{color: 'fg.default'}}>
                {name}
              </Link>
            </Box>
            <Box as="p" sx={{m: 0, fontSize: 0}}>
              <Link href={ownerPath({owner: name})} sx={{color: 'fg.default'}}>
                {name}
              </Link>
            </Box>
          </Box>
        </Box>
        <Button
          leadingVisual={HeartIcon}
          size="small"
          sx={{
            mt: condensed ? 3 : 0,
            '.octicon-heart': {
              color: 'sponsors.fg',
              transition: 'transform .15s cubic-bezier(0.2, 0, 0.13, 2)',
              transform: 'scale(1)',
              animationName: 'pulse-in',
              animationDuration: '.5s',
            },
            ':hover .octicon-heart': {
              transform: 'scale(1.1)',
            },
          }}
        >
          Sponsor
        </Button>
      </Box>
    </Box>
  )
}

try{ FundingDialog.displayName ||= 'FundingDialog' } catch {}
try{ GiHubFundingComponent.displayName ||= 'GiHubFundingComponent' } catch {}