import type {Extension} from '@codemirror/state'
import type {EditorView} from '@codemirror/view'
import {moveCursorToPositionAndScroll} from '@github-ui/code-mirror/utils'
import type {CodeMirrorSpacingOptions} from '@github-ui/code-view-types'
import {lazy, Suspense, useCallback, useMemo} from 'react'

import {LoadingFallback} from '../../SuspenseFallback'
import {FileUpload} from '../FileUpload'
import {defaultCodeMirrorSpacingOptions} from './CodeMirrorSpacingControls'

const CodeMirrorNext = lazy(() => import('@github-ui/code-mirror'))

export interface CodeMirrorOptions {
  dragDrop?: boolean
  indentUnit?: number
  indentWithTabs?: boolean
  lineWrapping?: boolean
}

interface ReactCodeMirrorProps extends CodeMirrorOptions {
  fileName: string
  spacingOptions?: CodeMirrorSpacingOptions
  value?: string
  onChange?: (value: string) => void
  onPreview?: () => void
  onSave?: () => void
  focusOnNextRender?: React.MutableRefObject<boolean>

  // upload specific props
  repositoryId?: string | number
  markdownDocsUrl?: string
  uploadPolicyPath: string
  uploadExtensions: string[]

  extensions?: Extension[]
}

export const ReactCodeMirror = ({
  value = '',
  fileName,
  focusOnNextRender,
  onChange,
  onPreview,
  onSave,
  repositoryId,
  spacingOptions,
  uploadPolicyPath,
  uploadExtensions,
  markdownDocsUrl,
  extensions = [],
}: ReactCodeMirrorProps) => {
  const onCodeMirrorNextUpdate = useCallback(
    (newValue: string) => {
      if (onChange) {
        onChange(newValue)
      }
    },
    [onChange],
  )

  const onHashChange = (editor: EditorView, hash: string) => {
    const result = /#?(?:L)(\d+)(?:C(\d+))?(-L(\d+)(?:C(\d+))?)?/gi.exec(hash)

    if (!result) return

    const startLine = result[1] ? parseInt(result[1]) : undefined
    const startColumn = result[2] ? parseInt(result[2]) : undefined
    const endLine = result[4] ? parseInt(result[4]) : undefined
    const endColumn = result[5] ? parseInt(result[5]) : undefined

    if (startLine === undefined) {
      return
    }

    moveCursorToPositionAndScroll(editor, startLine, endLine, startColumn, endColumn)
  }

  // drag and drop and file upload are kept in sync for now
  const fileExtension = fileName.includes('.') ? fileName.split('.').pop() ?? '' : ''
  const fileUploadEnabled = ['md', 'markdown', 'mkd'].includes(fileExtension)

  const codeMirrorSpacingOptions = {
    indentMode: spacingOptions?.indentMode || defaultCodeMirrorSpacingOptions.indentMode!,
    indentSize: spacingOptions?.indentSize || defaultCodeMirrorSpacingOptions.indentSize!,
    lineWrapping: spacingOptions?.lineWrapping || defaultCodeMirrorSpacingOptions.lineWrapping!,
  }

  const codeMirrorOptions = useMemo(() => {
    return {
      indentUnit: parseInt(codeMirrorSpacingOptions.indentSize),
      indentWithTabs: codeMirrorSpacingOptions.indentMode === 'tab',
      lineWrapping: codeMirrorSpacingOptions.lineWrapping === 'on',
      dragDrop: !fileUploadEnabled, // drag and drop is disabled when file upload is enabled
    }
  }, [
    codeMirrorSpacingOptions.indentSize,
    codeMirrorSpacingOptions.indentMode,
    codeMirrorSpacingOptions.lineWrapping,
    fileUploadEnabled,
  ])

  const keybindings = [
    {
      key: 'Shift-Mod-p',
      run: () => {
        onPreview?.()
        return true
      },
    },
    {
      key: 'Mod-s',
      run: () => {
        onSave?.()
        return true
      },
    },
  ]

  let hashChangeListener: () => void

  return (
    <FileUpload
      allowDragAndDrop={true}
      allowedExtensions={uploadExtensions}
      markdownDocsUrl={markdownDocsUrl}
      repositoryId={repositoryId}
      uploadPath={uploadPolicyPath}
      stickyFooter={true}
      disabled={!fileUploadEnabled}
    >
      <Suspense fallback={<LoadingFallback />}>
        <span id="codemirror-label" className="sr-only">{`Editing ${fileName} file contents`}</span>
        <CodeMirrorNext
          value={value}
          fileName={fileName}
          onChange={onCodeMirrorNextUpdate}
          ariaLabelledBy="codemirror-label"
          spacing={codeMirrorOptions}
          enableFileUpload={fileUploadEnabled}
          extensions={extensions}
          isHpc={true}
          keyBindings={keybindings}
          focusNextRenderRef={focusOnNextRender}
          onCreateEditor={editor => {
            if (!editor.hasFocus) {
              editor.focus()
            }

            hashChangeListener = () => {
              onHashChange(editor, window.location.hash)
            }

            window.addEventListener('hashchange', hashChangeListener)
            onHashChange(editor, window.location.hash)
          }}
          onDestroyEditor={() => {
            window.removeEventListener('hashchange', hashChangeListener)
          }}
        />
      </Suspense>
    </FileUpload>
  )
}

try{ CodeMirrorNext.displayName ||= 'CodeMirrorNext' } catch {}
try{ ReactCodeMirror.displayName ||= 'ReactCodeMirror' } catch {}