import {verifiedFetchJSON} from '@github-ui/verified-fetch'
import {AlertIcon} from '@primer/octicons-react'
import {Flash, Octicon} from '@primer/react'
import {useEffect, useState} from 'react'

type CheckReadinessResponse = {
  anyRunnersConfigured: boolean
}

export function WorkflowEditor({
  helpUrl,
  isEnterprise,
  repositoryActionsEnabled,
  repositoryActionsReadinessPath,
}: {
  helpUrl: string
  isEnterprise: boolean
  repositoryActionsEnabled: boolean
  repositoryActionsReadinessPath: string
}) {
  const [needsRunnersConfigured, setNeedsRunnersConfigured] = useState<boolean>(false)

  useEffect(() => {
    const getRunnerConfigurationRequired = async () => {
      try {
        const response = await verifiedFetchJSON(repositoryActionsReadinessPath)

        if (!response.ok) {
          setNeedsRunnersConfigured(false)
          return
        }

        const data = (await response.json()) as CheckReadinessResponse
        setNeedsRunnersConfigured(!data.anyRunnersConfigured)
      } catch (error) {
        setNeedsRunnersConfigured(false)
      }
    }

    if (isEnterprise && repositoryActionsEnabled) {
      getRunnerConfigurationRequired()
    }
  }, [isEnterprise, repositoryActionsEnabled, repositoryActionsReadinessPath])

  return (
    <>
      <span
        id="workflow-short-sha-warning-url"
        className="d-none"
        data-help-url={`${helpUrl}/actions/learn-github-actions/security-hardening-for-github-actions#using-third-party-actions`}
      />
      <span
        id="workflow-on-schedule-url"
        className="d-none"
        data-on-schedule-url={`${helpUrl}/actions/reference/workflow-syntax-for-github-actions#onschedule`}
      />
      <span
        id="workflow-dispatch-help-url"
        className="d-none"
        data-help-url={`${helpUrl}/actions/reference/events-that-trigger-workflows#workflow_dispatch`}
      />
      <span
        id="workflow-shell-help-url"
        className="d-none"
        data-help-url={`${helpUrl}/actions/using-workflows/workflow-syntax-for-github-actions#jobsjob_idstepsshell`}
      />

      {needsRunnersConfigured && (
        <Flash variant="warning" sx={{mb: 2}} data-testid="needs-runner-warning">
          <Octicon icon={AlertIcon} />
          This workflow will fail until runners are configured for the repository. Finish setting up GitHub Actions by{' '}
          <a
            className="Link--inTextBlock"
            href={`${helpUrl}/actions/hosting-your-own-runners/adding-self-hosted-runners`}
          >
            adding runners
          </a>
          .
        </Flash>
      )}
    </>
  )
}

try{ WorkflowEditor.displayName ||= 'WorkflowEditor' } catch {}