import type {DiffLine, DiffLines} from '@github-ui/code-view-types'
import {lineAnchorFrom, lineOrientationFrom} from '@github-ui/diffs/diff-line-helpers'
import {DiffLinePart, EmptyDiffLine, HunkHeaderDiffLine} from '@github-ui/diffs/DiffParts'
import type {DiffAnchor} from '@github-ui/diffs/types'

export function DiffPreview({diffData, fileName}: {diffData: DiffLines[]; fileName: string}) {
  const fileAnchor: DiffAnchor = `diff-${fileName}`

  return (
    <table>
      <tbody>
        {diffData[0]!.leftLine.left === 0 && diffData[0]!.rightLine.right === 0 && <tr />}
        {diffData.map((line, index) => {
          return (
            <tr key={index}>
              {renderLine(fileAnchor, true, true, line.leftLine)}
              {renderLine(fileAnchor, false, true, line.rightLine)}
            </tr>
          )
        })}
        {!diffData[diffData.length - 1]!.leftLine.noNewLine && !diffData[diffData.length - 1]!.rightLine.noNewLine && (
          <tr>
            <HunkHeaderDiffLine
              currentLine={{
                left: null,
                right: null,
                type: 'HUNK',
                html: '',
              }}
              isLeftColumn={true}
              isSplit={true}
            />
          </tr>
        )}
      </tbody>
    </table>
  )
}

function renderLine(fileAnchor: DiffAnchor, isLeftColumn: boolean, isSplit: boolean, line: DiffLine) {
  const lineOrientation = isSplit && isLeftColumn ? 'left' : lineOrientationFrom(line.type)
  const lineNumber = lineOrientation === 'left' ? line.left : line.right
  const lineAnchor = lineAnchorFrom(fileAnchor, lineOrientation, lineNumber ?? 0)

  switch (line.type) {
    case 'EMPTY':
      return <EmptyDiffLine isLeftColumn={isLeftColumn} />
    case 'HUNK':
      return <HunkHeaderDiffLine currentLine={line} isLeftColumn={isLeftColumn} isSplit={isSplit} />
    case 'ADDITION':
    case 'DELETION':
    case 'CONTEXT':
    case 'INJECTED_CONTEXT':
      return <DiffLinePart isLeftColumn={isLeftColumn} isSplit={isSplit} line={line} lineAnchor={lineAnchor} />
    default:
      throw new Error(`cannot handle type ${line.type}`)
  }
}

try{ DiffPreview.displayName ||= 'DiffPreview' } catch {}