import {AlertIcon} from '@primer/octicons-react'
import {Flash, Octicon} from '@primer/react'

export function TranscodingBanner({detectedEncoding}: {detectedEncoding: string}) {
  return (
    <Flash variant="warning" sx={{mb: 3}}>
      <Octicon icon={AlertIcon} />
      We’ve detected the file encoding as <strong>{detectedEncoding}</strong>. When you commit changes we will transcode
      it to <strong>UTF-8</strong>.
    </Flash>
  )
}

try{ TranscodingBanner.displayName ||= 'TranscodingBanner' } catch {}