import {GitIgnoreDropdown, type GitIgnoreDropdownProps} from '@github-ui/git-ignore-dropdown'
import {verifiedFetchJSON} from '@github-ui/verified-fetch'
import {useEffect, useState} from 'react'

interface Props {
  onTemplateSelect(templateContent: string): void
  sx?: GitIgnoreDropdownProps['buttonSx']
}

export function GitIgnoreTemplateSelector({onTemplateSelect, sx}: Props) {
  const [selectedTemplate, setSelectedTemplate] = useState<string>()

  async function onSelect(template: string) {
    setSelectedTemplate(template)
    const content = await fetchTemplateContent(template)
    content && onTemplateSelect(content)
  }

  // Reset cache on component unmount
  useEffect(() => clearGitignoreTemplateCache, [])

  return (
    <GitIgnoreDropdown
      selectedTemplate={selectedTemplate}
      onSelect={onSelect}
      buttonLabel="Choose .gitignore template"
      buttonSx={{...sx}}
    />
  )
}

const templatesMap = new Map<string, string>()

export function clearGitignoreTemplateCache() {
  templatesMap.clear()
}
async function fetchTemplateContent(templateName: string): Promise<string | null> {
  const template = templatesMap.get(templateName)
  if (template) {
    return template
  }

  const response = await verifiedFetchJSON(`/site/gitignore/${encodeURI(templateName)}`)
  if (!response.ok) {
    return null
  }
  try {
    const content = await response.text()
    templatesMap.set(templateName, content)
    return content
  } catch {
    return null
  }
}

try{ GitIgnoreTemplateSelector.displayName ||= 'GitIgnoreTemplateSelector' } catch {}