import '@github/file-attachment-element'

import {useCSRFToken} from '@github-ui/use-csrf-token'
import {MarkdownIcon} from '@primer/octicons-react'
import {Octicon, Spinner, Tooltip} from '@primer/react'
import React from 'react'

import {buildExtensionsSentence} from '../../utilities/file-extensions'

interface FileUploadProps {
  allowedExtensions: string[]
  allowDragAndDrop: boolean
  markdownDocsUrl?: string
  repositoryId?: string | number
  uploadPath: string
  stickyFooter?: boolean
  disabled?: boolean
}

export function FileUpload({
  allowedExtensions,
  allowDragAndDrop,
  children,
  markdownDocsUrl,
  repositoryId,
  uploadPath,
  stickyFooter = false,
  disabled = false,
}: React.PropsWithChildren<FileUploadProps>) {
  const friendlyExtensionSentence = buildExtensionsSentence(allowedExtensions)
  const uploadToken = useCSRFToken(uploadPath, 'post')
  const [isFocused, setIsFocused] = React.useState(false)

  return (
    <file-attachment
      class={!disabled ? 'js-upload-markdown-image is-default file-editor-upload react-file-upload' : undefined}
      input={!disabled ? 'blob-dragged-file-input' : undefined}
      data-upload-repository-id={!disabled ? repositoryId : undefined}
      data-upload-policy-url={!disabled ? uploadPath : undefined}
      data-upload-container-type={!disabled ? 'blob' : undefined}
      data-upload-container-id={!disabled ? repositoryId : undefined}
    >
      {children}
      <input type="hidden" data-csrf="true" className="js-data-upload-policy-url-csrf" value={uploadToken} />
      <label
        hidden={disabled}
        className={`text-normal drag-and-drop hx_drag-and-drop d-flex flex-justify-between border-0 border-top border-dashed ${
          stickyFooter ? 'position-sticky bottom-0' : 'position-relative'
        }  ${isFocused ? 'file-input-focused' : ''}`}
      >
        <input
          id="blob-dragged-file-input"
          type={'file'}
          accept={allowedExtensions.join(',')}
          multiple={true}
          onFocus={() => setIsFocused(true)}
          onBlur={() => setIsFocused(false)}
          className="manual-file-chooser manual-file-chooser-transparent top-0 right-0 bottom-0 left-0 width-full ml-0 form-control rounded-top-0"
        />
        <span
          className="color-bg-subtle position-absolute top-0 left-0 rounded-bottom-2 width-full height-full"
          style={{pointerEvents: 'none'}}
        />
        <span className="position-relative pr-2" style={{pointerEvents: 'none'}}>
          <span className="default">
            Attach files by {allowDragAndDrop ? 'dragging & dropping, ' : ''}selecting or pasting them.
          </span>
          <span className="loading">
            <Spinner size={'small'} sx={{verticalAlign: 'text-bottom', marginRight: 1}} />
            <span className="js-file-upload-loading-text" data-file-upload-message="Uploading your files…">
              Uploading your files…
            </span>
          </span>
          <span className="error bad-file">
            We don’t support that file type.
            <span className="drag-and-drop-error-info">
              <span className="btn-link">Try again</span> with a {friendlyExtensionSentence}.
            </span>
          </span>
          <span className="error bad-permissions">
            Attaching documents requires write permission to this repository.
            <span className="drag-and-drop-error-info">
              <span className="btn-link">Try again</span> with a {friendlyExtensionSentence}.
            </span>
          </span>
          <span className="error repository-required">
            We don’t support that file type.
            <span className="drag-and-drop-error-info">
              <span className="btn-link">Try again</span> with a {friendlyExtensionSentence}.
            </span>
          </span>
          <span className="error too-big js-upload-too-big">
            {/* This HTML has been moved off the render thread.
              The error message will come from the server response
            and be inserted in the DOM by JavaScript */}
          </span>
          <span className="error empty">
            This file is empty.
            <span className="drag-and-drop-error-info">
              <span className="btn-link">Try again</span> with a file that’s not empty.
            </span>
          </span>
          <span className="error hidden-file">
            This file is hidden.
            <span className="drag-and-drop-error-info">
              <span className="btn-link">Try again</span> with another file.
            </span>
          </span>
          <span className="error failed-request">
            Something went really wrong, and we can’t process that file.
            <span className="drag-and-drop-error-info">
              <span className="btn-link">Try again.</span>
            </span>
          </span>
        </span>
        <a
          className="Link--muted position-relative d-inline"
          href={markdownDocsUrl}
          target="_blank"
          data-ga-click="Markdown Toolbar, click, help"
          rel="noreferrer"
        >
          <Tooltip aria-label="Styling with Markdown is supported" direction="nw">
            <Octicon icon={MarkdownIcon} sx={{verticalAlign: 'text-bottom'}} />
          </Tooltip>
        </a>
      </label>
    </file-attachment>
  )
}

try{ FileUpload.displayName ||= 'FileUpload' } catch {}