import {AlertIcon} from '@primer/octicons-react'
import {Flash, Octicon} from '@primer/react'

export function MixedLineEndingsBanner() {
  return (
    <Flash variant="warning" sx={{mb: 3}}>
      <Octicon icon={AlertIcon} />
      We’ve detected the file has mixed line endings. When you commit changes we will normalize them to{' '}
      <strong>Windows-style</strong> (<kbd>CR</kbd>
      <kbd>LF</kbd>).
    </Flash>
  )
}

try{ MixedLineEndingsBanner.displayName ||= 'MixedLineEndingsBanner' } catch {}