import {
  acceptCompletion,
  type Completion,
  type CompletionContext,
  type CompletionResult,
  insertCompletionText,
  pickedCompletion,
  selectedCompletion,
  startCompletion,
} from '@codemirror/autocomplete'
import type {Command, EditorView} from '@codemirror/view'
import {autocomplete} from '@github-ui/code-mirror/autocomplete'
import {helpPanelConstructor} from '@github-ui/code-mirror/help-panel'
import {getCursor, getIndentationUnit, getLine, offsetToPos} from '@github-ui/code-mirror/utils'

import type {Snippet, Suggestion} from './autocomplete-util'
import {getIndentation, replaceWhitespaces} from './utils'
import {isMacOS} from '@github-ui/get-os'
import {html, render} from 'lit-html'

interface CustomCompletion extends Completion {
  snippet: Snippet
  from: number
  to: number
}

export function yamlAutocomplete(completionContext: (context: CompletionContext) => Promise<CompletionResult | null>) {
  return autocomplete(
    completionContext,
    [
      {key: 'Tab', run: acceptCompletionAndStartNext},
      {key: 'Enter', run: acceptCompletionAndStartNext},
    ],
    true, // override enter key for custom behavior
  )
}

export function yamlHelpPanel() {
  return helpPanelConstructor(autocompleteHelpPanel())
}

export function buildCompletions(suggestions: Suggestion[], cursor: number) {
  const completions: CustomCompletion[] = suggestions.map(suggestion => {
    return {
      label: suggestion.displayText,
      detail: suggestion.description,
      from: suggestion.replace ? cursor - suggestion.replace.length : cursor,
      to: cursor,
      apply: applySnippet,
      snippet: suggestion.snippet,
    }
  })

  const from = completions.length > 0 ? completions[0]!.from : cursor

  return {
    from,
    options: completions,
    filter: false,
  }
}

function applySnippet(view: EditorView, completion: Completion) {
  const {snippet} = completion as CustomCompletion
  return insertSnippet(view, completion, snippet, true)
}

function insertSnippet(view: EditorView, completion: Completion, snippet: Snippet, indent: boolean) {
  const {tokens} = snippet

  const tab = getIndentationUnit(view)

  for (let i = 0; i < tokens.length; i += 2) {
    const cursor = getCursor(view)
    const offset = offsetToPos(view, cursor.from)
    const ind = indent ? getIndentation(getLine(view, offset.line + 1).text) : ''

    const token = replaceWhitespaces(tokens[i]!, tab, ind)

    view.dispatch({
      ...insertCompletionText(view.state, token, view.state.selection.main.from, view.state.selection.main.to),
    })
  }

  // let codemirror know that we're done with the completion
  view.dispatch({
    annotations: pickedCompletion.of(completion),
  })
}

const acceptCompletionAndStartNext: Command = (view: EditorView) => {
  const selected = selectedCompletion(view.state) // the completion is active

  if (selected) {
    // if there is a selected completion, accept it and try to start the next one
    acceptCompletion(view)
    startCompletion(view)

    return true
  }

  // let this pass through to the default keymap
  return false
}

function autocompleteHelpPanel() {
  const dom = document.createElement('div')
  dom.className = 'cm-help-panel border-top'
  dom.setAttribute('data-testid', 'yaml-editor-help-footer')

  const appleShortcuts = isMacOS() ? html`or <kbd>Option + Space</kbd>` : html``

  const helpContent = html`
    <div id="yaml-editor-help-default">
      Use <kbd>Control + Space</kbd>
      ${appleShortcuts} to trigger autocomplete in most situations.
    </div>
    <div id="yaml-editor-help" hidden>
      <span className="js-yaml-editor-help-placeholder pr-1" />
      <span className="js-yaml-editor-help-tab"> Use <kbd>Tab</kbd> to navigate to the next placeholder. </span>
    </div>
  `

  render(helpContent, dom)

  return dom
}
