export function buildExtensionsSentence(extensions: string[]) {
  const friendlyExtensionNames = extensions.map((ext, index) => {
    const friendlyName = ext.replace('.', '').toUpperCase()

    if (index === extensions.length - 1 && extensions.length > 1) {
      return `or ${friendlyName}`
    } else {
      return friendlyName
    }
  })

  return friendlyExtensionNames.join(', ')
}
