import {Flash, Label, Link} from '@primer/react'

const ISSUE_FORM_HELP_URL =
  '/communities/using-templates-to-encourage-useful-issues-and-pull-requests/configuring-issue-templates-for-your-repository#creating-issue-forms'
const ISSUE_TEMPLATE_CONFIG_URL =
  '/articles/configuring-issue-templates-for-your-repository#configuring-the-template-chooser'
const ISSUE_TEMPLATE_HELP_URL = '/articles/about-issue-and-pull-request-templates'

export function IssueTemplateConfigBanner({helpUrl}: {helpUrl: string}) {
  return (
    <Flash sx={{mb: 3}}>
      Looks like this file is an <strong>issue template config</strong>. Need help? Check out the{' '}
      <Link inline href={`${helpUrl}${ISSUE_TEMPLATE_CONFIG_URL}`}>
        documentation.
      </Link>
    </Flash>
  )
}

export function IssueTemplateLegacyBanner({helpUrl}: {helpUrl: string}) {
  return (
    <Flash sx={{mb: 3}} variant="warning">
      You are using an old version of issue templates. Please update to the new issue template workflow.{' '}
      <Link inline href={`${helpUrl}${ISSUE_TEMPLATE_HELP_URL}`}>
        Learn more about issue templates.
      </Link>
    </Flash>
  )
}

export function IssueTemplateValidBanner({helpUrl, filePath}: {helpUrl: string; filePath: string}) {
  const isIssueForm = filePath.endsWith('.yml') || filePath.endsWith('.yaml')

  const issueHelpUrl = `${helpUrl}${isIssueForm ? ISSUE_FORM_HELP_URL : ISSUE_TEMPLATE_HELP_URL}`

  return (
    <Flash sx={{mb: 3}}>
      {isIssueForm && (
        <Label sx={{mr: 2}} variant="success">
          Beta
        </Label>
      )}
      Looks like this file is an issue template. Need help?{' '}
      <Link inline href={issueHelpUrl}>
        Learn more about issue templates.
      </Link>{' '}
      {isIssueForm && (
        <Link href="https://github.com/github/feedback/discussions/categories/issues-feedback">Give feedback.</Link>
      )}
    </Flash>
  )
}

try{ IssueTemplateConfigBanner.displayName ||= 'IssueTemplateConfigBanner' } catch {}
try{ IssueTemplateLegacyBanner.displayName ||= 'IssueTemplateLegacyBanner' } catch {}
try{ IssueTemplateValidBanner.displayName ||= 'IssueTemplateValidBanner' } catch {}