import type {EditorInfo} from '@github-ui/code-view-types'

// eslint-disable-next-line no-restricted-imports
import {devContainerEditor} from '../../../../github/editor/json-editors/devcontainer/devcontainer-editor-next'
// eslint-disable-next-line no-restricted-imports
import {dependabotEditor} from '../../../../github/editor/yaml-editors/dependabot/dependabot-editor-next'
// eslint-disable-next-line no-restricted-imports
import {workflowEditor} from '../../../../github/editor/yaml-editors/workflow/workflow-editor-next'

const VALID_DEVCONTAINER_PATH_GLOB =
  /\.devcontainer(?:(?:\/(?:[\w\-.'"][\w\-.'"\s]{0,62}[\w\-.'"]|[\w\-.'"]))?\/devcontainer)?\.json/

const isDevContainerEditorEnabled = (devContainerEditorEnabled: boolean, filePath: string) => {
  return devContainerEditorEnabled && VALID_DEVCONTAINER_PATH_GLOB.test(filePath)
}

export const isDependabotEditorEnabled = (dependabotEditorEnabled: boolean, filePath: string) => {
  return dependabotEditorEnabled && /^(\.github\/dependabot\.(yaml|yml))/i.test(filePath)
}

export const isStackEditorEnabled = (stacksEnabled: boolean, filePath: string) => {
  return stacksEnabled && /^(\.github\/stacks\/stack\.(yaml|yml))/i.test(filePath)
}

export const isWorkflowEditorEnabled = (actionsEnabled: boolean, filePath: string) => {
  return actionsEnabled && /^(\.github\/(workflows|workflows-lab)\/.+\.(yaml|yml))/i.test(filePath)
}

export function useEditors(editorInfo: EditorInfo, completeFilePath: string) {
  const workflowEditorEnabled = isWorkflowEditorEnabled(editorInfo.actionsEnabled, completeFilePath)
  const dependabotEditorEnabled = isDependabotEditorEnabled(editorInfo.dependabotEditorEnabled, completeFilePath)
  const devContainerEditorEnabled = isDevContainerEditorEnabled(editorInfo.devcontainerEditorEnabled, completeFilePath)

  return {
    workflowEditorEnabled,
    dependabotEditorEnabled,
    devContainerEditorEnabled,
  }
}

export function useEditorExtensions(editorInfo: EditorInfo, completeFilePath: string) {
  const enabledEditors = useEditors(editorInfo, completeFilePath)
  const {workflowEditorEnabled, dependabotEditorEnabled, devContainerEditorEnabled} = enabledEditors

  let editorExtensions = undefined

  if (workflowEditorEnabled) {
    editorExtensions = workflowEditor()
  } else if (dependabotEditorEnabled) {
    editorExtensions = dependabotEditor()
  } else if (devContainerEditorEnabled) {
    editorExtensions = devContainerEditor()
  }

  return editorExtensions
}
