import type {CompletionContext, CompletionResult} from '@codemirror/autocomplete'
import type {EditorView} from '@codemirror/view'
import {linter} from '@github-ui/code-mirror/linter'
import {getRange} from '@github-ui/code-mirror/utils'

import {autocomplete} from '../autocomplete-util'
import {buildCompletions, yamlAutocomplete, yamlHelpPanel} from '../codemirror-next-autocomplete-util'
import {getRootRule as getDependabotRootRule} from './dependabot-rules'
import lint from '../yaml-linters'
import {buildLintMarks} from '../../codemirror-next-linter-util'

export function dependabotEditor() {
  return [yamlAutocomplete(dependabotAutocompleteContext), yamlHelpPanel(), linter(dependabotLintSource)]
}

async function dependabotAutocompleteContext(context: CompletionContext): Promise<CompletionResult | null> {
  if (!context.explicit) return null

  const cursor = context.pos
  const textBeforeCursor = getRange(context.state, 0, cursor)

  const suggestions = await autocomplete(
    context.state.doc.toString(),
    textBeforeCursor,
    getDependabotRootRule,
    getDependabotRootRule().suggestionsForExpression!,
  )

  if (!suggestions) {
    return null
  }

  return buildCompletions(suggestions, cursor)
}

async function dependabotLintSource(view: EditorView) {
  const annotations = await lint(view.state.doc.toString(), getDependabotRootRule)

  return buildLintMarks(view, annotations)
}
