import memoize from '@github/memoize'
import type {SidepanelDocsHtml, SidepanelMarketplaceUrls} from '@github-ui/code-view-types'
import {useCurrentRepository} from '@github-ui/current-repository'
import {blobSidePanelMetadataPath} from '@github-ui/paths'
import {verifiedFetchJSON} from '@github-ui/verified-fetch'
import {useEffect, useState} from 'react'

interface SidepanelMetadata {
  docsHtml: SidepanelDocsHtml
  marketplaceUrls: SidepanelMarketplaceUrls
}

async function fetchJSON(url: string): Promise<SidepanelMetadata | undefined> {
  const response = await verifiedFetchJSON(url)
  if (response?.ok) {
    return await response.json()
  } else {
    return undefined
  }
}

const memoizeCache = new Map()
const memoizeFetchJSON = memoize(fetchJSON, {cache: memoizeCache})

export function useSidePanelMetadata(): [SidepanelMetadata | undefined, boolean, boolean] {
  const repo = useCurrentRepository()
  const [metadata, setMetadata] = useState<SidepanelMetadata>()
  const [error, setError] = useState(false)
  const [loading, setLoading] = useState(true)

  const metadataPath = blobSidePanelMetadataPath({owner: repo.ownerLogin, repo: repo.name})

  useEffect(() => {
    let cancelled = false
    const update = async () => {
      setLoading(true)
      setMetadata(undefined)
      const metadataResponse = await memoizeFetchJSON(metadataPath)

      if (cancelled) {
        return
      }

      try {
        if (metadataResponse) {
          setMetadata(metadataResponse)
        } else {
          setError(true)
        }
      } catch (e) {
        setError(true)
      }
      setLoading(false)
    }

    update()

    return function cancel() {
      cancelled = true
    }
  }, [metadataPath])

  return [metadata, loading, error]
}
