import {helpPanelConstructor} from '@github-ui/code-mirror/help-panel'
import {linter} from '@github-ui/code-mirror/linter'

import {html, render} from 'lit-html'
import {jsonLintSource} from '../json-codemirror-next-linter-util'

export function devContainerEditor() {
  const helpPanelContainer = document.createElement('div')
  render(helpPanel, helpPanelContainer)

  return [linter(jsonLintSource), helpPanelConstructor(helpPanelContainer)]
}

export const helpPanel = html`
  <div class="color-bg-subtle color-fg-muted px-2 py-1 m-0 border-top">
    <code id="dev-container-editor-help-default">
      See the
      <a target="_blank" rel="noopener noreferrer" href="https://containers.dev/implementors/json_reference/"
        >devcontainer.json</a
      >
      reference for more information
    </code>
  </div>
`
